<template>
  <div :class="css.wrapper">
    <div :class="css.container">
      <div :class="css.imageWrapper">
        <img
          src="./static/form-banner.png"
          data-src="./static/form-banner.png"
          alt="домики"
          :class="css.image"
          loading="lazy"
        />
      </div>
      <div :class="css.formWrapper">
        <div :class="css.tabsWrapper">
          <AuthTabs v-model="auth" />
        </div>
        <form
          ref="form"
          :class="[
            hasAnimateForm && (auth === 0 ? css.formLeft : css.formRight)
          ]"
          @submit.prevent="submit"
        >
          <div :class="css.marginBottom">
            <UiFormItem title="Имя пользователя" :error="error.username || error.all">
              <UiInput
                v-model="name"
                name="name"
                label="Имя"
                type="name"
                autocomplete="off"
                placeholder="Хозяин"
              />
            </UiFormItem>
          </div>
          <div :class="css.marginBottom">
            <UiFormItem title="Пароль" :error="error.password">
              <UiInput
                v-model="password"
                name="password"
                label="Password"
                type="password"
                placeholder="1234"
              />
            </UiFormItem>
          </div>
          <div :class="css.btnWrapper">
            <UiButton type="submit" :disabled="load">
              Вперед
            </UiButton>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState, mapMutations } from 'vuex';
import AuthTabs from '@/components/AuthTabs';
import { FormItem, Button, Input } from '@/components/UI';
import { isEmptyObject } from '@/lib/isEmpty';
// import { DashboardPage } from '@/router/routing';

import css from './AuthPage.css';

export default {
  name: 'AuthPage',
  data() {
    return {
      css,
      name: '',
      password: '',
      auth: 0,
      hasAnimateForm: false
    };
  },
  computed: {
    ...mapState({
      load: state => state.userStore.load,
      error: state => state.userStore.error
    })
  },
  watch: {
    auth() {
      this.hasAnimateForm = true;
      this.name = '';
      this.SET_ERROR({});
      this.password = '';
    }
  },
  methods: {
    ...mapActions(['login', 'registration']),
    ...mapMutations(['SET_ERROR']),
    async submit() {
      const data = {
        username: this.name,
        password: this.password
      };
      if (this.auth === 0) {
        await this.login(data);
      } else {
        await this.registration(data);
      }
      if (isEmptyObject(this.error)) {
        await this.$router.push('/');
      }
    }
  },
  components: {
    AuthTabs,
    UiButton: Button,
    UiInput: Input,
    UiFormItem: FormItem
  }
};
</script>
